.Square {
  background-color: rgb(255, 255, 255);
  height: 42px;
  width: 42px;
  border-radius: 1px;
  position: relative;
}

.highlight {
  border: solid black 2px !important;
}

.tw {
  background-color: #e77555;
  border-color: #ed7857;
}

.dw {
  background-color: #f2c4b7;
  border-color: #f9c9bc;
}

.dl {
  background-color: #b2d1de;
  border-color: #b3d3e0;
}

.tl {
  background-color: #3f8ead;
  border-color: #4191b1;
}

.multiplierText {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 600;
  letter-spacing: 1px;
  position: absolute;
  width: 100%;
  height: 100%;
  text-transform: uppercase;
  font-size: 8px;
  z-index: 5;
  display: flex;
  align-items: center;
  text-align: center;
}
